/* Cc nhm input chung */
.input-group {
    display: flex;
    flex-direction: column;
    margin-bottom: 10px;
    gap: 10px;
  
    label {
      margin-bottom: 5px;
    }
  
    input {
      padding: 10px;
      border-radius: 5px;
      border: 1px solid #ccc;
      outline: none;
    }
  }
  
  /* Nu bn c nhm input th hai, v d nh m t */
  .input-group1 {
    display: flex;
    flex-direction: column;
    margin-bottom: 10px;
    justify-content: center;
    align-items: center;
    gap: 10px;
  }
  
  /* Nhm chn danh mc (c th t tn ty , v d .select-group) */
  .select-group {
    display: flex;
    flex-direction: column;
    margin-bottom: 10px;
    gap: 10px;
  
    label {
      margin-bottom: 5px;
    }
  
    select {
      padding: 10px;
      border-radius: 5px;
      border: 1px solid #ccc;
      outline: none;
    }
  }
  
  /* Nt thm th loi */
  .btnaddtl {
    width: 100%;
    padding: 10px;
    border: none;
    border-radius: 5px;
    background-color: #616161;
    color: white;
    cursor: pointer;
    transition: background-color 0.2s ease;
  
    &:hover {
      background-color: #4f4f4f;
    }
  }
  
  /* Nhm cha nt (nu cn canh gia, thm justify-content) */
  .button-group {
    margin-top: 10px;
    display: flex;
    gap: 10px;
    /* V d:
    justify-content: center;
    */
  }
  