.blog-detail-container {
    display: flex;
    margin-right: 10%;
    padding-left: 10%;
    margin-top: 10px;
  
    @media screen and (max-width: 768px) {
      flex-direction: column; /* Chuyn thnh dng ct trn mn hnh nh */
      margin-right: 0;
      padding-left: 5%;
      padding-right: 5%;
    }
  }
  
  .blog-detail {
    h1 {
      margin-bottom: 1rem;
    }
    p {
      margin-bottom: 1rem;
    }
  
    /* m bo nh khng trn ra ngoi */
    img {
      max-width: 100%; /* Hnh nh khng vt qu kch thc container */
      height: auto;    /* Gi t l khung hnh */
      display: block;  /* Xa khong cch khng mong mun */
      margin: 0 auto;  /* Canh gia */
    }
  
    @media screen and (max-width: 768px) {
      h1 {
        font-size: 1.5rem;
      }
      p {
        margin-bottom: 1rem;
      }
    }
  }
  
  .category-sidebar {
    margin-left: 2rem;
  
    @media screen and (max-width: 768px) {
      margin-left: 0;
      margin-top: 2rem; /* Thm khong cch trn mn hnh nh */
    }
  }
  